import './Chatbox.css';
import logoScrimba from './assets/images/logo-scrimba.svg';
import sendIcon from './assets/images/send.svg';
import { progressConversation } from './index'
function Chatbox() {
  return (
    <main>
      <section className="chatbot-container">
        <div className="chatbot-header">
          <img src={logoScrimba} className="logo"></img>
          <p className="sub-heading">Knowledge Bank</p>
        </div>
        <div
          className="chatbot-conversation-container"
          id="chatbot-conversation-container"
        ></div>
        <form id="form" className="chatbot-input-container">
          <input name="user-input" type="text" id="user-input" required></input>
          <button id="submit-btn" className="submit-btn">
            <img src={sendIcon} className="send-btn-icon"></img>
          </button>
        </form>
      </section>
    </main>
  );
}
export default Chatbox;